.services-list {
  padding-bottom: 120px;
  &__breadcrumbs {
    padding-top: 60px;
    padding-bottom: 40px;
  }

  &__title {
    padding-bottom: 60px;
  }

  &__items {
    display: flex;
    flex-direction: column;
    &:not(:last-child){
      padding-bottom: 20px;
    }
  }
}

.breadcrumbs {
  &__link {
    color: var(--second-txt);
    font-size: 14px;
    font-weight: 400;
    line-height: 125%;
    &:not(:last-child)::after {
      content: "/";
      margin: 0 8px;
    }
  }

  &__link--active {
    color: var(--main-txt);
  }
}
.item-services {
  display: flex;
  &__img {
  }

  &__description {
    flex: 1 1 auto;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
  }
}

.description-item {
  &__link {
    padding: 15px 30px;
  }
}
