html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  color: var(--main-txt);
  font-size: 18px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  line-height: 150%;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  vertical-align: top;
}

a {
  color: inherit;
  text-decoration: none;
}
ul,
menu {
  list-style: none;
}

.site-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.main {
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
.btn {
  color: var(--white);
  font-size: 16px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  background-color: var(--main-bg);
  transition: background-color 0.2s ease-in;
  &:hover {
    background-color: var(--hover-bg);
    transition: background-color 0.2s ease-in;
    outline: none;
  }
  &:active {
    transition: background-color 0.2s ease-in;
    background-color: var(--active-bg);
    outline: none;
  }
  &:focus {
    transition: background-color 0.2s ease-in;
    background-color: var(--active-bg);
    outline: 2px solid var(--white, #fff);
  }
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

h1 {
  color: var(--main-txt);
  font-size: 48px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
h2 {
  color: var(--main-txt);
  font-size: 36px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
}
h3 {
  color: var(--main-txt);
  text-align: center;
  font-size: 24px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
}
h4 {
  color: var(--main-txt);
  font-size: 18px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

p {
  color: var(--main-txt);
  font-size: 16px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  line-height: 150%;
}
.btn-prev {
  display: inline-block;
  position: absolute;
  z-index: 2;
  cursor: pointer;
  transition: 0.2s ease-in;
  &:hover path {
    transition: 0.2s ease-in;
    stroke: var(--main-bg);
  }
}
.btn-next {
  display: inline-block;
  position: absolute;
  z-index: 2;
  cursor: pointer;
  transition: 0.2s ease-in;
  &:hover path {
    transition: 0.2s ease-in;
    stroke: var(--main-bg);
  }
}
.btn-play {
  transform: translate(-50%, -50% )scale(1);
  transition: 0.2s ease-in-out;
}
.btn-play--active {
  transform: translate(-50%, -50% )scale(0);
  transition: 0.2s ease-in-out;
}
.lg-image
{
  width: 120%;
  height: auto;
  margin: 0 auto;
  transition: 0.3s ease-in-out;
  cursor:zoom-in;
  &--zoom{
    transform: scale(1.5);
    transition: 0.3s ease-in-out;
    cursor:zoom-out;
    z-index: 25;

  }
}
