.hero {
  &__container {
    background-color: #f5f5f5;
    padding-bottom: 182px;
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__title {
    text-align: center;
    padding-bottom: 30px;
  }

  &__text {
    max-width: 908px;
    color: var(--main-txt);
    text-align: center;
    font-size: 18px;
    font-weight: 300;
    line-height: 150%;
  }
}
