@mixin select {
    position: relative;
  &__button {
    position: relative;
    display: block;
    width: 100%;

    padding-top: 13px;
    padding-bottom: 13px;
    padding-left: 20px;
    padding-right: 35px;

    cursor: pointer;
  }

  &__button:focus, .dropdown__button--active {
    outline: none;
  }


  &__list {
    display: none;
    position: absolute;
    left: 0;
    top: 52px;

    margin: 0;
    padding: 0;
    list-style-type: none;

    background: #FFFFFF;
    overflow: hidden;

    border-radius: 6px;
    width: 100%;

    z-index: 1;
    &--visible {
      display: block;
    }
  }


  &__list-item {
    margin: 0;
    padding: 0;
    border-bottom: 0px;
    padding: 13px 20px;
    cursor: pointer;
      &:first-child {
        border-radius: 6px 6px 0 0;
      }

      &:last-child {
        border-radius: 0 0 6px 6px;
      }

  }

  &__input-hidden {
    display: none;
  }
}
