.video {
  &__breadcrumbs {
    padding: 60px 0px 40px 0px;
  }

  &__title {
    padding-bottom: 60px;
  }

  &__list {
    margin-bottom: 120px;
  }

  &__item {
    display: flex;
    align-items: flex-start;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}

.item-video {
  &__inner {
    position: relative;
    margin-right: 30px;
  }

  &__player {
  }

  &__btn {
    position: absolute;
    top: 50%;
    left: 50%;
  }

  &__link {
    display: flex;
    padding: 30px;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;
  }

  &__title {
    padding-bottom: 20px;
    color: var(--main-txt);
    font-size: 24px;
    font-weight: 700;
    line-height: 150%;
  }

  &__text {
    flex: 1 1 auto;

  }

  &__button {
    margin-top: auto;
    padding: 15px 30px;
  }
}
