}
.form-calc {
  padding: 30px;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  max-width: 460px;
  background-color: #fff;
  &__file {
    position: relative;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--second-txt);
    &:hover {
      border-bottom: 1px solid var(--main-bg);
      outline: none;
    }
    &:focus {
      border-bottom: 1px solid var(--main-bg);
      outline: none;
    }
  }

  &__label {
    display: block;
    border-bottom: 1px solid var(--second-txt);
    &:hover {
      border-bottom: 1px solid var(--main-bg);
      outline: none;
    }
    &:focus {
      border-bottom: 1px solid var(--main-bg);
      outline: none;
    }
  }
  &__input {
    width: 100%;
    padding: 10px;
    color: var(--main-txt);
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    &::placeholder {
      color: var(--second-txt);
      font-weight: 300;
    }
    &:focus {
      outline: none;
    }
    &--textarea {
      width: 100%;
      height: 44px;
    }
  }
  &__btn {
    padding: 15px 30px;
  }
  &__text-area {
    flex: 1 0 auto;
    padding: 10px;
    resize: none;
    overflow: hidden;
    color: var(--main-txt);
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    vertical-align: middle;
    &::placeholder {
      color: var(--second-txt);
      font-weight: 300;
    }

    &:focus {
      outline: none;
    }
  }
}

.file {
  &__palceholder {
    color: var(--second-txt);
    font-family: Roboto;
    font-size: 16px;
    font-weight: 300;
    line-height: 150%;
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 4px 8px;
    color: var(--black, #333);
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 150%;
    background: var(--light-grey, #f5f5f5);
  }
  &__close {
    margin-left: 10px;
    cursor: pointer;
  }

  &__label {
    display: inline-block;
    cursor: pointer;
  }

  &__label--file {
    align-self: flex-end;
    position: relative;
    padding: 0 10px;
    &::after {
      content: "+";
      display: inline-block;
      font-size: 24px;
      color: var(--main-bg);
    }
  }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    font-size: 0;
    cursor: pointer;
  }
  &__file-load {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}
