.footer {
  margin-top: auto;
  background-color: var(--main-txt);
  &__inner {
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    font-weight: 700;
  }

  &__logo {
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: end;
  }
}

.list-items {
  &__item {
    &:not(:last-child){
      margin-right: 40px;
    }
  }
}
