.header {
  &_logo {
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.logo {
  &__img {
    margin-right: 40px;
  }

  &__text {
    max-width: 186px;
    color: var(--main-txt);
    font-weight: 300;
    line-height: 140%;
  }
}

.items-header {
  &__town-btn {
    display: flex;
    align-items: center;
  }

  &__tel {
    margin-left: 63px;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before {
      content: url("../img/tel.svg");
      margin-top: 10px;
      margin-right: 15px;
    }
  }
}
.town-btn {
  &__text {
    display: inline-block;
    margin-left: 8px;
    color: var(--main-txt);
    font-size: 14px;
    font-weight: 400;
    border-bottom: dashed 1px var(--second-txt);
  }
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.2);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;

  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%;
  }
  &--active {
    opacity: 1;
    visibility: visible;
    transition: all 0.3s ease-in-out;
  }
  &__container {
    max-width: max-content;
    position: relative;
    margin: 50px auto;
    vertical-align: middle;
    display: none;

    cursor: default;
    text-align: left;
    &--active{
      display: inline-block;
    }
    &__content{
      position: relative;

    }
  }
}

.tel {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link {
    color: var(--main-txt);
    font-size: 24px;
    font-weight: 700;
    line-height: normal;
  }

  &__btn {
    width: max-content;
    color: var(--second-txt);
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    outline: none;
    border-bottom: 1px dashed var(--second-txt);
  }
  &__modal {
    background-color: rgba(0, 0, 0, 0.2);
  }
}

.town-content {
  width: 1050px;
  &__header {
    padding: 20px 30px;
    text-align: start;
    background-color: #f5f5f5;
  }

  &__main {
    padding: 40px 30px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 530px;
    column-gap: 60px;
    background-color: #fff;
  }

  &__list {
    margin-bottom: 30px;
    color: var(--second-txt);
    font-size: 24px;
    font-weight: 700;
    line-height: 150%;
    list-style: none;
  }

  &__item {
    padding-top: 10px;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    transition: color 0.2s ease-in-out;
    cursor: pointer;
    &:hover {
      transition: color 0.2s ease-in-out;
      color: var(--main-bg);
    }
  }

  &__footer {
    padding-bottom: 30px;
    background-color: #fff;
    text-align: center;
  }
}

.modal-tel {
  &__container {
    max-width: max-content;
    padding: 0;
    border-radius: 0;
  }

  &__close {
    width: 24px;
    height: 24px;
    top: 26px;
    right: 30px;
    background-image: url("../img/close_md.svg");
    &:focus {
      outline: 1px solid #fff;
    }
  }
  &__title {
    padding: 20px 30px;
    background-color: var(--main-bg);
    display: block;
    color: var(--white);
    text-align: center;
    font-size: 24px;
    font-weight: 700;
    line-height: 150%;
  }
}
.form-callback {
  padding: 30px;
  display: flex;
  flex-direction: column;
  row-gap: 30px;

  &__label {
    display: block;
  }

  &__input {
    width: 100%;
    padding: 10px;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 300;
    line-height: 150%;
    border-bottom: 1px solid var(--second-txt);
    &:hover :focus {
      border-bottom: 1px solid var(--main-bg);
      outline: none;
    }
    &:focus {
      border-bottom: 1px solid var(--main-bg);
      outline: none;
    }
  }

  &__btn {
    padding: 15px 30px;
  }
}
.dropdown {
  @include select;
  &__button {
    padding: 10px 34px 10px 10px;
    background-color: #fff;
    border: none;
    border-bottom: 1px solid var(--second-txt);
    text-align: start;
    color: var(--second-txt);
    font-size: 16px;
    font-weight: 300;
    line-height: 150%;
    &:hover {
      border-bottom: 1px solid var(--main-bg);
    }
    &:focus {
      border-bottom: 1px solid var(--main-bg);
    }
  }
  &__icon {
    width: 24px;
    height: 24px;
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%) rotate(0deg);
    transition: 0.2s ease-in;
    right: 0;
    pointer-events: none;
    &--active {
      transform: translateY(-50%) rotate(180deg);
      transition: 0.2s ease-in;
    }
  }

  &__list {
    top: 45px;
    background: var(--white, #fff);
    box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
    border-radius: 0;
  }

  &__list-item {
    padding: 15px 20px;
    border-bottom: 2px solid #f5f5f5;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    &:first-child {
      border-radius: 0;
    }

    &:last-child {
      border-radius: 0;
    }

    &:hover {
      color: var(--main-bg);
    }
  }

  &__input-hidden {
    display: none;
  }
}
.custom-checkbox {
  @include custom-checkbox;
}
