// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: 1220px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --main-bg: #1FAB58;
  --second-bg: #F5F5F5;
  --hover-bg: #EB8A1D;
  --active-bg: #DB801A;
  --main-txt: #333;
  --second-txt: #7D7D7D;
  --white: #FFF;
}
