/* stylelint-disable */

@mixin custom-checkbox {
  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    user-select: none;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 300;
    line-height: normal;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: -2px;
      border: 2px solid var(--second-txt);
      width: 24px;
      height: 24px;
      box-sizing: border-box;
      transition: 0.2s ease-in-out;
    }
    &::after{
      content: url("../img/svg/check.svg");
      display: inline-block;
      position: absolute;
      left: 0;
      top: -3px;
      opacity: 0;
      visibility: hidden;
      transition: 0.2s ease-in-out;
    }
  }
}

.custom-checkbox__field:checked + .custom-checkbox__content::before {
  transition: 0.2s ease-in-out;
  background-color: var(--main-bg);
  border: none;
}
.custom-checkbox__field:checked + .custom-checkbox__content::after{
  opacity: 1;
  visibility: visible;
  transition: 0.2s ease-in-out;
}
.custom-checkbox__content:hover::before{
  background-color: #F5F5F5;
}


.custom-checkbox__field:disabled + .custom-checkbox__content {
  color: var(--second-txt);
  pointer-events: none;
}
