.faq {
  &__breadcrumbs {
    padding: 60px 0px 40px 0px;
  }

  &__title {
    padding-bottom: 60px;
  }

  &__inner {
    padding-bottom: 120px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__item {
    position: relative;
    overflow: hidden;
    max-width: 1010px;
    padding: 20px 40px 20px 40px;
    border-radius: 20px;
    border: 1px solid var(--green, #1fab58);
    background: var(--white, #fff);
  }
}

.item-faq {
  &__btn {
    display: block;
    width: 100%;
    color: #2b2b2b;
    font-size: 18px;
    font-weight: 700;
    position: relative;
    text-align: start;
  }

  &__icon {
    position: absolute;
    top: -10px;
    right: 0px;
    display: inline-block;
    width: 40px;
    height: 40px;
    stroke: var(--second-txt);
    fill: none;
    border-radius: 50%;
    transform: rotate(0deg);
    transition: 0.2s ease-in;
    transition: 0.2s ease-out;
    &:hover {
      background-color: #f5f5f5;
      stroke: var(--main-bg);
      transition: 0.2s ease-in;
    }
  }
  &__icon--active {
    transform: rotate(180deg);
    transition: 0.2s ease-out;
  }
  &__text {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    will-change: max-height;
    padding-left: 10px;
    margin-top: 0px;
    padding-bottom: 0px;
    color: var(--main-txt);
    display: flex;
    position: relative;
    transition: 0.2s ease-out;
    &::before {
      content: "";
      position: absolute;
      top: 10px;
      left: 0px;
      display: inline-block;
      width: 4px;
      height: 4px;
      background-color: #eb8a1d;
      border-radius: 50%;
    }
  }
  &__text--active {
    max-height: 0;
    opacity: 1;
    margin-top: 20px;
    padding-bottom: 10px;

  }
}
