.reviews {
  &__breadcrumbs {
    padding: 60px 0px 40px 0px;
  }

  &__title {
    padding-bottom: 60px;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    margin-bottom: 120px;
  }
}
.review {
  display: flex;
  padding: 30px;
  border-radius: 20px;
  border: 1px solid var(--main-bg);
  &:not(:last-child){
    margin-bottom: 20px;
  }
  &__img {
    flex: 0 0 auto;
    margin-right: 30px;
    width: 100px;
    height: 100px;
  }

  &__description {
  }
}
.description-review {
  &__title {
    padding-bottom: 20px;
    color: var(--black, #2b2b2b);
    font-family: Roboto;
    font-size: 24px;
    font-weight: 700;
  }
}
