.header-menu {
  position: sticky;
  top: 0;
  z-index: 3;
  &__container {
    background-color: var(--main-bg);
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    &:hover {
      background-color: var(--hover-bg);
      transition: background-color 0.2s ease-in;
    }
    &:active {
      background-color: var(--active-bg);
      transition: background-color 0.2s ease-in;
    }
    &:hover > .services__list {
      transition: 0.2s ease-in;
      opacity: 1;
      visibility: visible;
    }
  }
  &__icon {
    fill: none;
    margin-left: 10px;
    width: 24px;
    height: 24px;
    stroke: #fff;
    stroke-width: 3px;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 3vw;
    color: #fff;
    white-space: nowrap;
    font-size: 16px;
    font-weight: 700;
    &:focus {
      outline: 2px solid var(--white, #fff);
    }
  }
}
.services {
  &__list {
    background-color: #fff;
    position: absolute;
    top: 68px;
    left: -15px;
    z-index: 2;
    width: 25vw;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s ease-in;
  }

  &__item {
    position: relative;
    padding: 15px 35px 15px 15px;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    border-bottom: 2px solid var(--light-grey, #f5f5f5);
    &:hover {
      transition: color 0.2s ease-in;
      color: var(--main-bg);
    }
    &:hover > .item-services__icon {
      transition: stroke 0.2s ease-in;
      stroke: var(--main-bg);
    }
    &:hover > .item-services__list {
      transition: 0.2s ease-in;
      opacity: 1;
      visibility: visible;
    }
  }
}
.item-services {
  &__list {
    background-color: #fff;
    width: 25vw;
    position: absolute;
    top: 0;
    right: -25vw;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s ease-in;
  }
  &__part {
    position: relative;
    padding: 15px 20px;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    border-bottom: 2px solid var(--light-grey, #f5f5f5);
    &:hover {
      transition: color 0.2s ease-in;
      color: var(--main-bg);
    }
    &:hover > .item-services__icon {
      transition: stroke 0.2s ease-in;
      stroke: var(--main-bg);
    }
    &:hover > .part-services__list {
      transition: 0.2s ease-in;
      opacity: 1;
      visibility: visible;
    }
  }

  &__link {
    &:hover {
      transition: color 0.2s ease-in;
      color: var(--main-bg);
    }
  }
  &__icon {
    position: absolute;
    right: 20px;
    width: 24px;
    height: 24px;
    fill: none;
    stroke: var(--second-txt);
    vertical-align: middle;
  }
}
.part-services {
  &__list {
    background-color: #fff;
    width: 25vw;
    position: absolute;
    top: 0;
    right: -25vw;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s ease-in;
  }

  &__item {
    position: relative;
    padding: 15px 20px;
    color: var(--main-txt);
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    border-bottom: 2px solid var(--light-grey, #f5f5f5);
  }

  &__link {
    &:hover {
      transition: color 0.2s ease-in;
      color: var(--main-bg);
    }
  }
}
