.services {
  &__container{
    transform: translateY(-70px);
  }
  &__cards {
    display: grid;
    grid-template-columns: min-content min-content;
    grid-template-rows: 1fr 1fr;
    gap: 20px;
    justify-content: center;
    margin-bottom: 120px;
  }
  &__card {
    padding-bottom: 10px;
    max-width: 390px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #f5f5f5;
  }

  &__inner-txt {
    margin-bottom: 100px;
  }
  &__title {
    padding-bottom: 30px;
  }

  &__text {
    padding-bottom: 20px;
  }
}
.card {
  &__title {
    padding: 10px 20px 8px 20px;
    font-size: 16px;
  }

  &__text {
    text-align: center;
    padding: 0px 20px 8px 20px;
    font-size: 14px;
    font-weight: 700;
    line-height: 140%;
  }

  &__link {
    margin-top: auto;
    max-width: 144px;
    padding: 10px 30px;
  }
  &__img .image{
    width: 390px;
    height: auto;
  }
}
